p {
  margin: 0;
  padding: 0;
}

.form {
  display: flex;
  width: 100%;
  height: 100%;
  padding-inline: 2rem;
  padding-block: 1rem;
  color: #fff;
  text-shadow: -1px -1px 0 #000000, 1px -1px 0 #000000, -1px 1px 0 #030303,
    1px 1px 0 #000000;
  gap: 1rem;

  &__container {
    &-input {
      display: flex;
      width: 50%;
      height: 100%;
      flex-direction: column;
      gap: 1rem;
    }

    &-submit {
      display: flex;
      width: 50%;
      height: 100%;
      padding-bottom: 10%;
      align-items: end;
      justify-content: center;
    }
  }

  &__input {
    position: relative;
    display: flex;
    flex-direction: column;
  }
}

.input__error {
  position: absolute;
  top: 0;
  right: 0;
  color: #ca2a2a;
}

.button-submit {
  position: absolute;
  bottom: 15%;
  right: 20%;
  height: 100px;
  width: 100px;
  border: 3px solid #000;
  outline: 6px solid #fff;
  border-radius: 50%;
  background-color: #ca2a2a;
  font-family: inherit;
  font-size: 1.5rem;
  letter-spacing: inherit;

  transition-duration: 200ms;

  &:hover {
    transform: scale(1.1);
    cursor: pointer;
  }
}
